<template>
  <Card>
    <p slot="title">
      添加内容
    </p>
    <Form
      ref="formValidate"
      :model="formValidate"
      :rules="ruleValidate"
      :label-width="80"
      style="width: 500px"
    >
      <FormItem label="栏目" prop="column">
        <Select v-model="formValidate.column" placeholder="请选择">
          <Option value="beijing">口岸资讯</Option>
        </Select>
      </FormItem>
      <FormItem label="标题" prop="name">
        <Input v-model="formValidate.name" placeholder="请输入"></Input>
      </FormItem>
      <FormItem label="封面图" prop="downloadUrl">
        <div
          class="demo-upload-list"
          v-for="(item, index) in defaultList"
          :key="index"
        >
          <img :src="item.url" />
          <div class="demo-upload-list-cover">
            <Icon
              type="ios-eye-outline"
              @click.native="handleView(item)"
            ></Icon>
            <Icon
              type="ios-trash-outline"
              @click.native="handleRemove(item, index)"
            ></Icon>
          </div>
        </div>
        <Upload
          ref="upload"
          :show-upload-list="false"
          :default-file-list="defaultList"
          :action="upload_url"
          :data="fileData"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :on-progress="handleProgress"
          :on-error="handleError"
          :before-upload="onBefore"
          :on-success="handleSuccess"
          multiple
          type="drag"
          style="display: inline-block;width:58px;"
        >
          <div style="width: 58px;height:58px;line-height: 58px;">
            <Icon type="ios-camera" size="20"></Icon>
          </div>
        </Upload>
        <Modal title="图片" v-model="visible">
          <img :src="showUrl" v-if="visible" style="width: 100%" />
        </Modal>
      </FormItem>
      <FormItem label="状态" prop="state">
        <CheckboxGroup v-model="formValidate.state">
          <Checkbox label="Eat">首页推荐</Checkbox>
          <Checkbox label="Sleep">置顶</Checkbox>
        </CheckboxGroup>
      </FormItem>
      <FormItem label="介绍" prop="info">
        <Input
          v-model="formValidate.info"
          type="textarea"
          :autosize="{ minRows: 2, maxRows: 5 }"
          placeholder="请输入"
        ></Input>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="handleSubmit('formValidate')"
          >提交</Button
        >
        <Button style="margin-left: 8px" @click="back">返回</Button>
      </FormItem>
    </Form>
  </Card>
</template>
<script>
import { appVersionNew } from '@/api/application.js'
import { SERVER_CODE, SERVER_URL } from '@/utils/constants.js'
export default {
  data() {
    return {
      fileData: { fileType: 1004, project: 'PORT', password: 'heihei' },
      defaultList: [],
      upload_url: SERVER_URL.UPLOAD_URL,
      showUrl: '',
      visible: false,
      formValidate: {
        column: '',
        name: '',
        downloadUrl: '',
        state: [],
        info: ''
      },
      ruleValidate: {
        name: [
          {
            required: true,
            message: '不能为空',
            trigger: 'blur'
          }
        ],
        column: [
          {
            required: true,
            message: '请选择栏目',
            trigger: 'change'
          }
        ],
        state: [
          {
            required: true,
            type: 'array',
            min: 1,
            message: '请选择至少一个',
            trigger: 'change'
          },
          {
            type: 'array',
            max: 2,
            message: 'Choose two hobbies at best',
            trigger: 'change'
          }
        ],
        downloadUrl: [
          {
            required: true,
            message: '不能为空',
            trigger: 'blur'
          }
        ],
        info: [
          {
            required: true,
            message: '不能为空',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  mounted() {},
  activated() {
    this.formValidate = {
      downloadUrl: '',
      info: '',
      name: ''
    }
    this.defaultList = []
  },
  methods: {
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          appVersionNew(this.formValidate)
            .then(({ code, msg }) => {
              if (code === SERVER_CODE.CODE_200) {
                this.$Message.success('应用新增成功！')
                this.$store.state.applicationStatus = true
                setTimeout(() => {
                  this.$router.go(-1)
                }, 2000)
              } else {
                this.$Message.info(msg)
                console.log('应用新增失败')
              }
            })
            .catch(error => {
              console.log(error)
            })
            .finally(() => {})
        }
      })
    },
    handleView(item) {
      this.showUrl = item.url
      this.visible = true
    },
    onBefore(file) {
      let exceededNum = file.name.lastIndexOf('.')
      let exceededStr = file.name
        .substring(exceededNum + 1, file.name.length)
        .trim()
      if (
        exceededStr != 'jpg' &&
        exceededStr != 'png' &&
        exceededStr != 'jpeg'
      ) {
        this.$Notice.warning({
          title: '文件格式错误',
          desc: file.name + '文件格式不正确, 请选择 jpg png jpeg '
        })
        return false
      }
      if (file.size > 102400 * 2000) {
        this.$Notice.warning({
          title: '文件大小错误',
          desc: file.name + '文件大小太大，超过200M'
        })
        return false
      }
      return true
    },
    handleError() {
      this.$Message.info('上传失败！')
    },
    handleRemove(item, index) {
      //删除文件
      console.log(item)
      console.log(index)
      this.defaultList = []
      this.formValidate.downloadUrl = ''
    },
    handleProgress() {
      //文件上传时的钩子
    },
    handlePreview(file) {
      //点击已上传的文件链接时的钩子
      location.href = file.url
    },
    handleSuccess(res, file) {
      //上传成功只保留一份文件
      this.defaultList = []
      let data = { name: file.name, url: res.data }
      this.defaultList.push(data)
      this.formValidate.downloadUrl = res.data
      this.$Message.success('上传成功！')
    },
    back() {
      this.$router.go(-1)
    }
  }
}
</script>
<style scoped>
.demo-upload-list {
  display: inline-block;
  width: 60px;
  height: 60px;
  text-align: center;
  line-height: 60px;
  border: 1px solid transparent;
  border-radius: 4px;
  overflow: hidden;
  background: #fff;
  position: relative;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
  margin-right: 4px;
}
.demo-upload-list img {
  width: 100%;
  height: 100%;
}
.demo-upload-list-cover {
  display: none;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.6);
}
.demo-upload-list:hover .demo-upload-list-cover {
  display: block;
}
.demo-upload-list-cover i {
  color: #fff;
  font-size: 20px;
  cursor: pointer;
  margin: 0 2px;
}
</style>
