/**
 * 应用管理接口
 */

import http from '@/utils/http.js'
import { API_URL } from '@/utils/constants.js'
import Qs from 'qs'

/**
 * 应用列表
 * @param {*} params  查询参数
 */
export const appVersionList = params => {
  return http.get(API_URL.APPVERSION_LIST_URL, { params })
}

/**
 * 应用详情
 * @param {*} params
 */
export const appVersion = params => {
  return http.get(API_URL.APPVERSION_URL + '/' + params.id, {})
}

/**
 * 应用版本删除
 * @param {*} params
 */
export const appVersionDelete = params => {
  return http.post(API_URL.APPVERSION_DELETE_URL, Qs.stringify(params), {
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  })
}

/**
 * 应用版本修改
 * @param {*} params
 */
export const appVersionUpdate = params => {
  return http.post(API_URL.APPVERSION_UPDATE_URL, Qs.stringify(params), {
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  })
}

/**
 * 应用版本新增
 * @param {*} params
 */
export const appVersionNew = params => {
  return http.post(API_URL.APPVERSION_NEW_URL, Qs.stringify(params), {
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  })
}
